.GYM__Footer1{
   background-color: #111111;
   color: white;
   display: flex;
   flex-direction: row;
   justify-content: space-around;
   padding: 50px 0;
}

.GYM__Footer{
    display: flex;
    flex-direction: column;
}

.GYM__Footer-SocialMedia-Head{
    margin-bottom: 10px;
}

.GYM__Footer-SocialMedia-Content p{
    margin: 10px 0;
    font-size: 20px;
}

.GYM__Footer-SocialMedia-Head{
    margin-bottom:30px;
}

.GYM__Footer-SocialMedia-Head h3{
    font-size: 1.5rem;
}

.GYM__Footer2-CopyRight{
    text-align: center;
    background-color: black;
    color: white;
    padding:70px 0;
}

@media screen and (max-width:800px) {
    .GYM__Footer1{
        display: flex;
        flex-direction: column;
        padding: 40px;
    }

    .GYM__Footer-SocialMedia-Head h3{
        padding-top: 15px;
    }

    .GYM__Footer-SocialMedia-Content p{
        margin: 15px 0;
    }
}