.Gym__Header-img img{
  height:100%;
  width:100%;
}

.Gym__Header-img{
  height:100%;
  width:100%;
  flex: 2;
}

.Gym__Header{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: black;
}

.Gym__Header-Heading{
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
 
  
}

.Gym__Header-Heading h1{
  width: 70%;
  color: white;
  font-size: 4rem;
  text-align: center;
}


@media screen and (max-width:700px) {
.Gym__Header{
  display: flex;
  flex-direction: column-reverse;
  justify-content:center;
  align-items: center;
}

.Gym__Header-Heading{
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  text-align: center;
  padding: 50px 0;
}

.Gym__Header-Heading h1{
  font-size: 1.6rem;
}

}