.GYM__Plane-Child-AllPlanes{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.GYM__Plane-Heading{
    text-align: center;
    font-size: 2rem;
}


.GYM__Plane-Child-AllPlane-Head {
    border-color: white;
    border: solid 1px;
    padding: 45px 0;
}

.GYM__Plane-Child-AllPlane-Head h3 {
  text-align: center; 
  border-color: white;
  font-size: 1.5rem;
} 

.GYM__Plane-Child{
    border-color: white;
    padding: 60px;
}

.GYM__Plane-Child-AllPlane-Amount{
    text-align: center;
    border-color: white;
    border: solid 1px;
    padding: 45px 0;
}

.GYM__Plane-Child-AllPlane-Amount h3{
      border-color: white;
}

.GYM__Plane-Child-AllPlane-Para p{
    width: 80%;
    text-align: center;
    font-size: 17px;
    font-size: 1.1rem;
}

.GYM__Plane-Child-AllPlane-Para{
    display: flex;
    justify-content: center;
    align-items: center;
    border-color: white;
    border-left: solid 1px;
    border-right: solid 1px;
    padding: 45px 0;
}

@media screen and(max-width:800px) {
    .GYM__Plane-Child-AllPlane{
        width: 20%;
    }
}

