.GYM__Plane{
    background-color: black;
    color: white;
    padding:50px 0;
}

.GYM__Plane-AllPlanes{
    display: flex;
    justify-content: space-around;
}

@media screen and (max-width:800px) {
    .GYM__Plane-Heading h1{
        font-size: 2rem;
        padding-top: 50px;
    }
    .GYM__Plane-AllPlanes{
        display:flex;
        flex-direction: column;
    }

    .GYM__Plane{
        padding: 0;
    }

}