.GYM__Class-Information{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 100px;
}
.GYM__Class{
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #111111;
}

.GYM__Class-Information img{
    width: 40%;
    height: 40%;
    opacity: 5;
    cursor: pointer;
    filter: grayscale(100%);
    transition: ease-out 0.5s;
    
}

.GYM__Class-Information p{
    width: 30%;
    text-align: left;
    font-size: 1.5rem;
    color: white;
}

.GYM__Class-Heading h1{
    margin: 5rem 0;
    color: white; 
    font-size: 3rem;
    text-align: center;
}


.GYM__Class-Information img:hover{
    filter: grayscale(0);
    transition: ease 0.6s;
}

.GYM__Class-Types{
    color: white;
}

.GYM__Class-Articles{
    background-color:black;
    padding-bottom: 60px;
}


@media screen and (max-width:800px){
    .GYM__Class-Information{
        display: flex;
        flex-direction: column;
    }

    .GYM__Class-Information p{
      width: 80%;
      text-align: left;
      font-size: 1.1rem;
      margin-left: 10px;
    }

    .GYM__Class-Heading h1{
        margin: 30px;
        font-size:2rem;
    }

    .GYM__Class-Information{
        width:100%;
        padding-bottom: 50px;
    }

     .GYM__Class-Information img{
        width: 100%;
        padding-top: 50px;
     }
     
}