.Gym__Navbar{
    display: flex;
    justify-content: space-between;
    padding: 30px 100px; 
    font-size: 20px;
    background-color:black;
    color: white;
}

.Gym__Navbar-Menu{
    display: flex;
    flex-direction: row;
}

.Gym__Navbar-Menu-items{
   padding: 0 30px;
}

.Gym__Navbar-HandburgerMenu{
    display: none;
    cursor: pointer;
}

.Gym__Navbar-HandburgerMenu-overlay{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    width: 100%;
    transition: 0.5s ease;
    flex-direction: column;
    z-index: 5;
    background-color: black;
}

.overlayClose{
 cursor: pointer;
 position: absolute;
 top: 20px;
 right: 20px;
}

.Gym__Navbar-HandburgerMenu-overlay-items{
    font-size: 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 70%;
}

.Gym__Navbar-Menu-items{
   cursor: pointer;
  
}

.Gym__Navbar-Menu-items:hover{
    text-decoration: underline;
    transition: ease 1.5s; 
}


@media screen and (max-width:1150px) {
    .Gym__Navbar-HandburgerMenu{
        display: flex;
    }
    .Gym__Navbar-Menu-items{
        padding:10px 0;
    }

    .Gym__Navbar{
        padding: 30px  40px;
    }

    .Gym__Navbar-Menu{
        display: none;
    }
}