.GYM__Nutrition{
    display: flex;
    flex-direction: row;  
    background-color: #111111;
    color: white;

}

.GYM__Nutrition-Content{
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: flex-start;
     margin-left: 50px;
}

.GYM__Nutrition-Content p{
    margin: 40px 0;
    font-size: 1.5rem;
    width: 75%;
}

.GYM__Nutrition-Content h1{
    font-size: 3rem;
}

.GYM__Nutrition-Content button{
    width: 15%;
    height: 14%;
}

.GYM__Nutrition-ScrollImage{
  width: 100%;
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
}

.GYM__Nutrition-ScrollImage img{
    width: 55%;
    padding-left: 40px;
    filter:grayscale(100%);
    transition: ease-out 0.5s;
    margin: 40px 0;
}

.GYM__Nutrition-ScrollImage img:hover{
    filter: grayscale(0);
    transition:ease 0.5s;
}

.GYM__Nutrition-ScrollImage::-webkit-scrollbar{
    display: none;
}

@media screen and (max-width:800px) {
    .GYM__Nutrition{
        display: flex;
        flex-direction: column;
        padding-left: 8px;
    }

    .GYM__Nutrition-Content{
        margin: 0;
        padding-left: 40px;
        padding-top: 40px;
    }

    .GYM__Nutrition-Content p{
        width: 90%;
        margin: 25px 0;
        font-size: 1.1rem;
    }

    .GYM__Nutrition-Content h1{
        font-size:2rem;
    }

    .GYM__Nutrition-Content button{
       width: 23%;
       padding: 6px;
      
    }

    .GYM__Nutrition-ScrollImage img{
        padding: 0;
        padding-right: 10px;
    }

    .GYM__Nutrition-ScrollImage{
        padding-left: 38px;  
    }
}