.GYM__Class-ClassComponents{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.GYM__Class-ClassComponents h1{
    margin:80px 0;
    font-size: 3rem;
}

.GYM__Class-ClassComponents img {
    width: 30%;
    border-radius: 3%;
    filter: grayscale(100%);
   
    transition: ease-out 0.5s;
}

.GYM__Class-ClassComponents p{
    font-size:1.5rem;
    width: 45%;
    margin-top: 50px;
    padding:0 70px;
    text-align: left;
}

.GYM__Class-ClassComponents img:hover{
      filter: grayscale(0);
      cursor: pointer;
      transition: ease 0.6s;
}

.GYM__Class-ClassComponents-img-content{
    display: flex;
    justify-content: center;
    align-items: center;
}


@media screen and (max-width:800px) {
    .GYM__Class-ClassComponents{
     width: 100%;
    }

    .GYM__Class-ClassComponents-img-content{
        display: flex;
        flex-direction: column;
    }

    .GYM__Class-ClassComponents h1{
        margin: 0;
        font-size: 2rem;
        padding:50px 0;
    }

    .GYM__Class-ClassComponents img{
        width: 75%;
    }

    .GYM__Class-ClassComponents-img-content p{
        width: 100%;
        padding: 0 40px;
        text-align: left;
        font-size: 1.1rem;
        margin-left: 20px;

    }


}